import React, { useState, useRef, useEffect, useCallback } from 'react';
import Quagga from '@ericblade/quagga2';
import Scanner from './Scanner';

const App = () => {
    const [scanning, setScanning] = useState(false); // toggleable state for "should render scanner"
    const [cameraId, setCameraId] = useState(null); // id of the active camera device
    const [cameraError, setCameraError] = useState(null); // error message from failing to access the camera
    const [code, setCode] = useState(null);
    const scannerRef = useRef(null); // reference to the scanner element in the DOM
    const [permissionInProgressEnabled, setPermissionInProgressEnabled] = useState( false );
    const [permissionNoticeVisible, setPermissionNoticeVisible] = useState( true );

    useEffect(() => {
        const enableCamera = async () => {
            await Quagga.CameraAccess.request(null, {
                audio: false,
                video: isMobile2() ? {
                    facingMode: {
                        exact: 'environment',
                    },
                } : true,
            } );
            setPermissionInProgressEnabled( true );
        };
        const disableCamera = async () => {
            await Quagga.CameraAccess.release();
        };
        const enumerateCameras = async () => {
            const cameras = await Quagga.CameraAccess.enumerateVideoDevices();
            console.log('Cameras Detected: ', cameras);
            return cameras;
        };
        enableCamera()
        .then( () => {
            setScanning(!scanning);
            setPermissionNoticeVisible( false );
            setPermissionInProgressEnabled( false );
        } )
        .then(disableCamera)
        .then(enumerateCameras)
        .catch((err) => setCameraError(err));
        return () => disableCamera();
    }, []);

    return (
        <div style={ permissionNoticeVisible ? {
            margin: '150px 20px 20px',
        } : {
            margin: '20px',
        } }>
            {
                permissionNoticeVisible && (
                    <p>
                        In order to proceed please "Allow" Camera usage.
                    </p>
                )
            }
            {
                permissionInProgressEnabled && (
                    <p>
                        Checking.... "Allow" Camera permission.
                    </p>
                )
            }
            {
                cameraError ?
                    <p>ERROR INITIALIZING CAMERA ${JSON.stringify(cameraError)} -- DO YOU HAVE PERMISSION?</p> :
                    null
            }
            {
                code && (
                    <div style={ {
                        display: 'flex',
                        alignItems: 'center',
                    } }>
                        <p>
                            Result
                        </p>
                        <p style={ {
                            marginLeft: '20px',
                            fontWeight: '700',
                        } }>
                            { code }
                        </p>
                    </div>
                )
            }
            <div ref={scannerRef} style={{position: 'relative'}}>
                <canvas
                    className="drawingBuffer"
                    style={{
                        position: 'absolute',
                        top: '0px',
                    }}
                    width="640"
                    height="480"
                />
                {
                    scanning ?
                        <Scanner
                            scannerRef={ scannerRef }
                            cameraId={ cameraId }
                            setCode={ setCode }
                            setScanning={ setScanning }
                            constraints={ {
                                audio: false,
                                video: isMobile2() ? {
                                    facingMode: {
                                        exact: 'environment',
                                    },
                                } : true,
                            } }
                        /> :
                        null
                }
            </div>
        </div>
    );
};

export default App;

function isMobile() {
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry/i.test(navigator.userAgent);
}

function isMobile2() {
    return isMobile() || /Windows Mobile|iemobile|Puffin|Silk|Opera Mini/i.test( navigator.userAgent );
}